/* 
0 - 600px:    Phone
600 - 900px:  Tablet portrait
900 - 1200px: Tablet landscape
1200 - 1800p
1800px + :    Big desktop

//Breakpoints
-phone
-tab-port
-tab-land
-big-desktop
*/
@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 37.5625em) and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 56.3125em) and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

body {
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu", sans-serif;
}

.container {
  min-height: 90vh;
}

.header {
  background: #ff525d;
  background: linear-gradient(90deg, #ff8f70 10%, #ff525d 70%);
  position: relative;
  min-height: 80vh;
  overflow: hidden;
  justify-content: space-evenly;
}
.header img {
  position: absolute;
  display: block;
  max-width: 150%;
  top: -140%;
  left: 0%;
}
.header-nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 15vh;
  font-size: 1.6rem;
  color: white;
}
.header-nav-nav {
  z-index: 999;
  display: flex;
}
.header-nav-nav #logo {
  margin-right: 8rem;
}
.header-nav-nav ul {
  display: flex;
}
.header-nav-nav ul li {
  margin-right: 3rem;
}
.header-nav-nav ul li:last-child {
  margin-right: 0rem;
}
.header-nav-user {
  z-index: 999;
  display: flex;
}